/**
 *  新添按钮颜色值
 */

button.is-custom.el-button.el-button--primary {
  background-color: #FF5A5F;
  border-color: transparent;
  color: #fff;
}

button.is-custom.el-button--primary:hover {
  background-color: rgba(255, 90, 95, 0.7);
  border-color: transparent;
  color: #fff;
}

button.is-custom.el-button.el-button--default {
  background-color: #fff;
  border-color: #FF4500;
  color: #FF4500;
}

button.is-custom.el-button.el-button--default:hover {
  background-color: #fff;
  border-color: #FF5A5F;
  color: #FF5A5F;
}

button.is-custom:focus, .button.is-custom.is-focused {
  border-color: transparent;
  box-shadow: 0 0 0.5em rgba(0, 209, 178, 0.25);
  color: #fff;
}

button.is-custom:active, .button.is-custom.is-active {
  background-color: #FF4500;
  border-color: transparent;
  box-shadow: inset 0 1px 2px rgba(10, 10, 10, 0.2);
  color: #fff;
}

button.is-custom.is-inverted {
  background-color: #fff;
  color: #FF5A5F;
}

button.el-button.is-custom.is-disabled {
  background-color: #eef1f6;
  color: #666;
}

button.is-custom.is-inverted:hover {
  background-color: #f2f2f2;
}

button.is-custom.is-loading:after {
  border-color: transparent transparent #fff #fff !important;
}

button.is-custom.is-outlined {
  background-color: transparent;
  border-color: #FF5A5F;
  color: #FF5A5F;
}

button.is-custom.is-outlined:hover, .button.is-custom.is-outlined:focus {
  background-color: #FF5A5F;
  border-color: #FF5A5F;
  color: #fff;
}

button.is-custom.is-outlined.is-loading:after {
  border-color: transparent transparent #FF5A5F #FF5A5F !important;
}

