
.process-item {
  margin: 0 0 30px 0;
}
.process-item p {
  line-height: 2;
  font-size: 1.7rem;
  font-weight: bold;
}
.progress {

  min-width: 174px;
  padding: 20px;
  border: 1px solid #ccc;
}
.progress-m {
  padding: 20px 0;
  border: none;
  white-space: nowrap;
  overflow-x: scroll;
}
.cover-m {
  overflow: hidden;
  height: 158px;
}
.progress-m div.process-item {
  display: inline-block;
  background: #fafafa;
  border-radius: 4px;
  height: 140px;
  padding: 10px 15px;
  margin: 0 0 0 15px;
}
.process-item-m p {
  font-size: 1.4rem;
}



